

import { useState } from "react";
import Head from '../../components/Head'
import GoodsCard from '../../components/GoodsCard'
import ImgBox from '../../components/ImgBox'
import goods02 from '../../assets/goods02.png'

import './index.css'
import Tabs from '../../components/Tabs'
import NumOperation from "../../components/NumOperation";
import CartSuspend from "../../components/CartSuspend";

export default function GoodsDetail() {
    const [tabIndex, setTabIndex] = useState(0);

    const handleTabChange = (index) => {
        setTabIndex(index)
    }

    return (
        <div className="goods-detail">
            <CartSuspend/>
         
            <div className="detail-content">
                <div className="left">
                    <div className="goods-info">
                        <div className="goods-img">
                            <ImgBox data={[{ src: goods02, alt: '' }]}></ImgBox>
                        </div>
                        <div className="goods-sku">
                            <div className="name"></div>
                            <div className="sales"> 9999999</div>
                            <div className="price">
                                <span className='market-price-label'></span>
                                <span className='unit'>TWD</span>
                                <span className='price-num'>2323232</span>
                                <div className='original-price'>
                                     TWD 9999999
                                </div>
                            </div>
                            <div className="buyNum">
                                <span></span>
                               <NumOperation/>
                            </div>
                            <div className='buy-btn'></div>
                        </div>
                    </div>
                    <div className="goods-detail-render">
                        <Tabs onChange={handleTabChange} data={['', '']} />
                        <div className="content">
                            {
                                tabIndex === 0 && <div className="tab-content">
                                    <div className="desc">
                                        <p></p> <br />
                                    </div>
                                </div>
                            }
                            {
                                tabIndex === 1 && <div className="tab-content">
                                    <div className="desc">
                                        <p></p> <br />
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                </div>
                <div className="right">
                    <div className="title">^</div>
                    <div className='recommend-list'>
                        <GoodsCard></GoodsCard>
                    </div>
                </div>
            </div>
        </div>
    )
}