

import Input from '../../Form/Input'
import Radio from '../../Form/Radio'
import RadioGroup from '../../Form/RadioGroup'
import Select from '../../Form/Select'
import './index.css'
export default function AddressModal(props) {
    const {visible=false,onClose=()=>{}}=props;


    const handleClose = () => {
        onClose();
    }
    const handleConfirm = () => {

    }
    return (
        <div className="address-modal" style={{ display: visible? 'block' : 'none' }}>
            <div className='modal-mask' onWheel={(e) => { e.stopPropagation() }}></div>
            <div className="modal-content">
                <div className="modal-header"></div>
                <div className="modal-body">

                    <div className="form-card">
                        <div className='form-item-label'></div>
                        <div>
                            <Input label="" placeholder="" />
                            <div style={{ marginTop: '10px' }}></div>
                            <Input label="" placeholder="" />
                        </div>
                        <div><RadioGroup>
                            <Radio label="" ></Radio>
                            <Radio label="" ></Radio>
                        </RadioGroup></div>

                        <div>
                            <span className='required'>*</span>
                        </div>
                        <div className='address-district'>
                            <Select options={[{ name: '', value: 'beijing' }, { name: '', value: 'shanghai' }, { name: '', value: 'guangzhou' }, { name: '', value: 'shenzhen' }]}></Select>
                            <Select options={[{ name: '', value: 'dongchengqu' }, { name: '', value: 'xichengqu' }, { name: '', value: 'chaoyangqu' }, { name: '', value: 'haidianqu' }]}></Select>
                            <Select options={[{ name: '', value: 'fengtaiqu' }, { name: '', value: 'shijingshanqu' }, { name: '', value: 'tongzhouqu' }, { name: '', value: 'shunyiqu' }]}></Select>
                          <div style={{gridColumn: '1 / span 4', marginTop: '10px'}}>  <Input label="" placeholder="" /></div>
                        </div>

                    </div>
                </div>
                <div className='modal-footer'>
                    <div className='btn ' onClick={handleClose}></div>
                    <div className='btn btn-primary' onClick={handleConfirm}></div>
                </div>
            </div>
        </div>
    )
}